// Api
import Api from "api/news.js";
// js
import { GetLocalS } from "common/important.js";

export default {
	data() {
		return {
            // 
            showSelectModel: false,
            // 
            listLoading: false,
            // 
            groupList: [],
            // 
            selectIndex: -1,
            // 
            showDelComfir: false,
            // 
            showAddModel: false,
            // 
            groupName: '',
            // 
            isCollect: false,
            // id
            newsId: 0,
            // 
            newsCont: {
                group:{
                    id: 0,
                    name: ''
                },
                isCollect: false
            },
        }
    },
    methods:{
        // 
        collect(){
            // 
            if(GetLocalS('project_token')) {
                // 
                if(this.newsCont.isCollect){
                    // 
                    this.showDelComfir = true;
                    return true;
                }
                // 
                else{
                    this.showSelectModel = true;
                    this.getGroups();
                }                    
            }
            else this.showWarnModel('', 'warning');
        },
        // 
        saveCollect(){
            if(this.newsCont.group.id == 0){
                this.showWarnModel('', 'warning');
                return false;
            }

            Api.AddArticle({
                declareId: this.newsId,
                groupId: this.newsCont.group.id
            })
            .then(res => {
                if(res.code == 200) {
                    this.hideSelectModel();
                }
                else this.showWarnModel(res.msg, 'warning');
            })
            .catch(err => console.log(err))
        },
        // 
        cancelCollect(){
            Api.DeleteArticle([this.newsId])
            .then(res => {
                this.pageLoading = false;
                if(res.code == 200){
                    this.hideDelModel();
                }
                else this.showWarnModel(res.msg, 'warning');
            })
            .catch(err => console.log(err))
        },
        // 
        hideDelModel(value){
            this.showDelComfir = value;
        },
        // 
        getGroups(){
            this.listLoading = true;
            Api.GetGroups()
            .then(res => {
                this.listLoading = false;
                if(res.code == 200){
                    this.groupList = res.data;
                }
                else{
                    this.showWarnModel(res.msg, 'warning');
                    this.showSelectModel = false;
                }
            })
            .catch(err => console.log(err))
        },
        // 
        hideSelectModel(){
            this.showSelectModel = false;
            this.newsCont.group.id = 0;
            this.selectIndex = -1;
        },
        // 
        getGroupSelect(item, index){
            this.newsCont.group.id = item.id;
            this.selectIndex = index;
        },
        // 
        showAddGroup(){
            this.showAddModel = true;
            this.groupName = '';
        },
        // 
        hideAddModel(value){
            this.showAddModel = value;
        },
        // 
        addGroup(){
            if(this.groupName == ''){
                this.showWarnModel('', 'warning');
                return false;
            }

            Api.AddGroup(this.groupName)
            .then(res => {
                this.pageLoading = false;
                if(res.code == 200){
                    this.getGroups();
                    this.showAddModel = false;
                }
                else this.showWarnModel(res.msg, 'warning');
            })
            .catch(err => console.log(err))
        }
    }
}


